@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@300&display=swap);
* {
  margin: 0;
}

body {
  background-color: rgb(234, 237, 237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.checkout {
  display: flex;
  padding: 20px;
  background-color: white;
  height: -webkit-max-content;
  height: max-content;
  margin-bottom: 100px;
}

.checkout__ad {
  width: 100%;
  margin-bottom: 10px;
}

.checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}

.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 300px;
  height: 130px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
  margin-top: 40px;
}

.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift > input {
  margin-right: 5px;
}

.subtotal > button {
  background: #fceb00;
  border-radius: 2px;
  width: 100%;
  height: 100px;
  border: 1px solid;
  border-color: #dae90e #e6ff02 #fbff00;
  color: #111;
}

.checkoutProduct {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
}

.checkoutProduct__info {
  padding-left: 20px;
}

.checkoutProduct__info > button {
  background: #35b68f;
  border: 1px solid;
  /* margin-top: 60px; */
  border-color: #42a834 #4f9c31 #297e84;
  color: #111;
  border-radius: 40px;
}

.checkoutProduct__image {
  object-fit: contain;
  width: 180px;
  height: 180px;
}

.checkoutProduct__rating {
  display: flex;
}

.checkoutProduct__title {
  font-size: 17px;
  font-weight: 800;
  margin-bottom: 30px;
  margin-top: 8px;
}

.topbar {
  flex: 0 0 100%;
  max-width: 100%;
  font-size: 14px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: 0;
  background-color: #395e63 !important;
  color: #999;
  margin-bottom: -13px;
  margin-top: -38px;
}

/* The Modal (background) */
.modal {
  position: fixed;
  z-index: 999;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

/* Modal Content */
.modal-content {
  background-color: #f6f6f8;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 40%;
  top: 30%;
  left: 0;
  transform: translate(0%, 0%);
}

/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.mainfull {
  width: 40% !important;
  height: 30%;
}

.submit {
  border-radius: 0 !important;
  line-height: 0.7 !important;
  font-weight: normal !important;
  background: #1abe9c !important;
  border: 1px solid #1abe9c !important;
}

.markermin {
  background: #1abe9c;
  padding: 11px;
  color: #ffff;
}

.modal-backdrop {
  z-index: 0;
}

.image-product {
  height: auto;
}

.test-div {
  border: 2px solid #ccc;
  /* width:600px; */
}
.test-div-inner {
  padding-bottom: 100%;
  background: #eee;
  height: 0;
  position: relative;
}
.test-image {
  width: 100%;
  display: block;
}

#forun{
background-color: #EFE1DD !important;
}

body {
  padding: 0;
  height: 100vh;
  margin-bottom: 100px;
}
.born {
  margin-top: 100px;
  margin-bottom: 300px;
}
.logo {
  box-sizing: border-box;
  margin: 0 auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.headline {
  display: block;
  margin-left: 42px;
  margin-top: -2.5rem;
  font-size: 3rem;
  font-family: "Readex Pro";
  margin-bottom: 7rem;
}

.container {
  /* display: flex; */
  width: 100vw;
  justify-content: space-around;
  flex-direction: row;
}
.box {
  height: 29rem;
  width: 37rem;

  display: inline-flex;
  flex-direction: column;

  border-radius: 2rem;
  z-index: 1100;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  transition: all 0.125s ease-out;
}
.burger {
  position: absolute;
}
/* .coke {
  margin-left: 39rem;
  margin-top: -9rem;
  transform: rotate(-45deg);
  z-index: 1;
} */
.burger {
  margin-left: -30rem;
  margin-top: 5rem;
  transform: rotate(-30deg);
  z-index: 1;
}
.box img {
  width: 8rem;
  height: 8rem;
  margin: 0 auto;
}
.b1 img {
  margin-top: 1rem;
}
.b2 img,
.b3 img {
  margin-top: 1rem;
}
.b1 {
  background-color: rgba(231, 141, 141, 0.41);
  border: none;
}
.b2 {
  background-color: rgba(107, 186, 58, 0.17);
  border: none;
}
.b3 {
  background-color: rgba(34, 34, 34, 0.2);
  border: none;
}
.box:hover {
  transform: scale(1.05);
}
.b1 a {
  margin-top: 7.5rem;

  font-family: "Readex Pro";
  font-size: min(1.5rem, 3ch);
  color: white;
  -webkit-text-stroke: 0.015rem rgb(255, 81, 58);
  text-decoration: none;
}
.b2 a {
  margin-top: 8rem;
  color: linear-gradient(seagreen, cyan);
  -webkit-text-stroke: 0.025rem rgb(83, 212, 139);
  font-family: "Readex Pro";
  font-size: min(1.5rem, 3ch);

  text-decoration: none;
}
.b3 .num {
  margin-top: 8rem;

  font-family: "Readex Pro";
  font-size: min(1.5rem, 3ch);
  -webkit-text-stroke: 0.001rem white;
  text-decoration: none;
}
@media (orientation: Portrait) {
  .container {
    width: 100vw;
    flex-direction: column;
    justify-content: center;
  }
  .box {
    margin: 0 auto;
    margin-bottom: 1rem;
  }
  .burger {
    width: 50vw;
    height: 30vh;
    margin-left: 0rem;
    margin-top: 25rem;
  }
  .coke {
    width: 60vw;
    max-width: 40vw;
    margin-left: 2rem;
    margin-top: -42rem;
  }
  .headline {
    margin-top: 3rem;
    margin-left: 0.5rem;
  }
}

.payment {
  background-color: white;
}
.payment__container > h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}

.payment__container > h1 a {
  text-decoration: none;
}

.payment__section {
  display: flex;
  padding: 20px;
  margin: 0 20px;
  border-bottom: 1px solid lightgray;
}

.payment__title {
  flex: 0.2 1;
}

.payment__address,
.payment__items,
.payment__details {
  flex: 0.8 1;
}

.payment__details > form {
  max-width: 400px;
}

.payment__details > h3 {
  padding-bottom: 20px;
}

.payment__details > form > div > button {
  background: #4b7ff0;
  border-radius: 10px;
  width: 100%;
  height: 60px;
  border: 5px solid;
  font-weight: bolder;
  margin-top: 10px;
  border-color: #2e1dcc #31759c #294984;
  color: rgb(255, 255, 255);
}

.orders {
    padding: 20px 80px;
}

.orders > h1 {
    margin: 30px 0;
}
.login {
  margin: 0;
  padding: 0;
  margin-top: 5rem;
  height: 75vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.login__container {
  height: 70vh;
  display: flex;
  width: -webkit-max-content;
  width: max-content;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-family: "Readex Pro", sans-serif;
}
p {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-gap: 2.25rem;
  gap: 2.25rem;

  border-radius: 3rem;
  padding: 7rem 2.5rem;
  transition: all 0.25s ease-out;
}

.login__container h2 {
  font-family: "Readex Pro", sans-serif;
}
.login__container form input {
  font-family: "Readex Pro", sans-serif;

  border-radius: 2rem;
  font-size: 2.25rem;
}

.login__signInButton {
  position: relative;
  width: -webkit-max-content;
  width: max-content;
  margin: 0 auto;
  font-family: "Readex Pro", sans-serif;
  margin-top: 4rem;
  font-size: 2.25rem;
  border-radius: 2.5rem;
}
.login__registerButton {
  font-family: "Readex Pro", sans-serif;
  font-size: 2.25rem;
  border-radius: 3rem;
}
.Burger {
  position: absolute;
  width: 48rem;
  height: 36rem;
  z-index: -1;
  left: 10%;
  right: 0;
  animation: Float 2s ease-in-out 0.1s infinite alternate;
}
@keyframes Float {
  100% {
    left: 0;
    transform: rotate(35deg);
  }
}
form:hover,
form:focus-within {
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  background-color: rgba(104, 218, 180, 0.1);
  box-shadow: 0.1rem 0.1rem 0.25rem 0rem rgb(182, 182, 182);
}
.Pizza {
  position: absolute;
  width: 40rem;
  height: 40rem;
  left: 65%;

  z-index: -10;
  animation: Float1 2s ease-in-out 0.1s infinite alternate;
}
@keyframes Float1 {
  100% {
    left: 55%;
    transform: rotate(-35deg) translateY(2rem);
  }
}

